/* ------------Common Start------------ */


:root {
    --white-color: #f1f2f6;
    --main-color: green;
    --text-color: #1e272e;

    --secondary-color: #f1f2f6;
    --secondary-text-color: #485460;
    --footer-color: #111111;
}

.page-heading {
    /* margin-top: 3rem; */
    padding: 3rem 0;
    text-align: center;
}

.page-heading h5 {
    text-transform: uppercase;
    font-size: 1rem;
    color: var(--main-color);
}

.page-heading p {
    font-size: 2rem;
    font-weight: 600;
    color: var(--black-color);
}

.card-container {
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 3rem;
}

section {
    min-height: 100vh;
}

.content-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

a {
    text-decoration: none;
}

.btn {
    padding: .5rem 1.4rem;
    font-size: 1.2rem;
}

.btn-main {
    background-color: var(--main-color);
    color: var(--white-color);
    font-weight: 600;
    border: .1rem solid var(--main-color);
}

.btn-main:hover {
    border: .1rem solid var(--main-color);
    color: var(--main-color);
}

.btn-outline-main {
    color: var(--main-color);
}

.btn-outline-main:hover {
    border: .1rem solid var(--main-color);
}

/* ------------Common End------------ */









/* ------------Header Start------------ */
header {
    z-index: 999;
    width: 100%;
    /* position: fixed; */
    background-color: var(--white-color);
}

.upper {
    padding: .4rem;
    color: var(--white-color);
    background-color: var(--main-color);
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 1.2rem;
    font-weight: 400;
}

.upper i {
    font-size: 1.3rem;
    color: var(--white-color);
}

.upper .contact-links a {
    margin: 0 1rem;
    color: var(--white-color);
}

.upper .contact-links a i {
    margin-right: .4rem;
}

.upper .social-media-icon a {
    margin: 0 1rem;
}

header .navbar .nav-item .nav-link {
    font-size: 1.1rem;
    color: var(--text-color);
    font-weight: 600;
    margin: 0 1.2rem;
}

header .navbar .nav-link {
    position: relative;
}

header .navbar-brand {
    font-size: 1.2rem;
    color: var(--main-color);
    font-weight: 700;
}

/* ------------Header End------------ */










/* ------------Home Start------------ */
#home {
    /* background-image: url("https://i0.wp.com/talkingcirclestherapy.com/wp-content/uploads/2020/09/couples-counseling-before-marriage.jpg?ssl=1"); */

    background-image: url("https://res.cloudinary.com/liaison-inc/image/upload/c_fill,f_auto,q_auto/services/thervo/backgrounds/marriage-counseling.jpg");
    /* background-color: skyblue; */

    background-position: top;
    background-size: cover;

}

#home .home-content h3 {
    text-shadow: 0 0 2px white;
    /* color: var(--main-color); */
    font-size: 4rem;
}

#home .home-content h3 span {
    color: var(--main-color);
}

#home .home-content p {
    font-size: 1.3rem;
}

/* ------------Home End------------ */













/* ------------Services Start------------ */
#services .card {
    border: none;
}

#services .card .box {
    display: flex;
    justify-content: center;
    align-items: center;
}

#services .card .card-body {
    text-align: center;
}

#services .card .box .inner i.bx {
    font-size: 5rem;
    color: var(--main-color);
}

#services .card .box .inner {
    padding: 2rem;
    border-radius: 50%;
    background-color: var(--secondary-color);
    position: relative;
    transition: .5s;
}

#services .card .box .inner:hover {
    background-color: var(--main-color);
}

#services .card .box .inner:hover i {
    color: var(--white-color);
}

#services .card .box .inner:hover span.count {
    color: var(--main-color);
    background-color: var(--secondary-color);
}

#services .card .box .inner span.count {
    position: absolute;
    top: .4rem;
    right: .4rem;

    font-weight: 600;
    padding: .3rem .5rem;
    border-radius: 50%;
    /* font-size: 1.1rem; */
    background-color: var(--main-color);
    color: var(--white-color);
    border: .2rem solid white;
    transition: .5s;
}


#services .service-content {
    color: var(--secondary-text-color);
    background-color: var(--secondary-color);
    padding: 3rem;
}

#services .service-content h5 {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1rem;
}

#services .service-content h4 {
    font-size: 2.2rem;
    font-weight: 700;
}

#services .service-content p {
    margin: 1rem 0;
}

#services .service-content a i {
    font-size: 3rem;
    margin-right: 1rem;
    color: var(--main-color);
}

#services .service-content a {
    display: flex;
    align-items: center;
    color: var(--secondary-text-color);
    text-transform: uppercase;
    font-weight: 500;
}


#services .our-services {
    color: var(--secondary-text-color);
    /* height: 75vh; */
}

#services .our-services .list-group a {
    padding: 1.3rem;
    font-size: 1.2rem;
    font-weight: 700;
}

#services .our-services .scrollspy-example {
    height: 75vh;
    overflow-y: scroll;
}

#services .our-services .scrollspy-example h4 {
    font-size: 1.8rem;
}

#services .our-services .scrollspy-example img {
    width: 100%;
    height: 50vh;
    margin-bottom: 2rem;
}

#services .our-services .scrollspy-example div {
    height: 75vh;
}

#services .our-services .list-group-item.active {
    background-color: var(--main-color);
    color: white;
}

/* ------------Services End------------ */





/* ------------Testimonial Start------------ */
.testimonial {
    background-image: url("https://www.toolshero.com/wp-content/uploads/2019/02/bonchek-steele-thinking-styles-toolshero.jpg");
    background-size: cover;
    background-position: top;
    background-attachment: fixed;
    min-height: 75vh;
    position: relative;

    margin-bottom: 6rem;
}

.testimonial .overlay {
    position: absolute;
    width: 100%;
    height: 75vh;
    background-color: rgba(2, 88, 2, 0.793);
    display: flex;
    align-items: center;
}

.testimonial .page-heading h5,
.testimonial .page-heading p {
    color: white;
}

.testimonial .page-heading h5 {
    font-size: 2rem;
}

.testimonial .page-heading p {
    font-size: 5rem;
    font-weight: 800;
}

.testimonial .card-container {
    margin: auto;
    width: 100%;
    position: absolute;
    bottom: -6rem;
    left: 0;
    right: 0;
    gap: 3rem;
}

.testimonial .card {
    border: 0;
    box-shadow: 0 0 1rem var(--text-color);
}

.testimonial .card-body {
    padding: 1.5rem;
    position: relative;
}

.testimonial .card-body i {
    position: absolute;
    top: -1.3rem;
    left: 2rem;
    padding: .7rem;
    font-size: 1.5rem;
    background-color: var(--main-color);
    color: var(--white-color);
    font-weight: 800;
    border-radius: 50%;
}

.testimonial .card-profile {
    display: flex;
    align-items: center;
}

.testimonial .card-profile img {
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    margin: .6rem;
}

/* ------------Testimonial End------------ */















/* ------------Plans start------------ */
#plans {
    background-color: var(--secondary-color);
}

#plans .card-container {
    display: grid;
    grid-template-columns: auto auto auto;
}

#plans .card {
    padding: 1rem;
    border-radius: 0;
    color: var(--secondary-color);
}

#plans .card .card-header {
    text-align: center;
    font-size: 4rem;
    background: transparent;
    color: var(--main-color);
}


#plans .card .card-header h5 {
    font-size: 1rem;
    font-weight: 700;
    color: var(--text-color);
}

#plans .card sup {
    top: -2rem;
    font-size: 1.7rem;
}

#plans .card sub {
    font-size: 1.7rem;
}

#plans .card .card-body ul li {
    display: flex;
    align-items: center;
    background: transparent;
    border: 0;
}

#plans .card .card-body i {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--main-color);
    margin-right: 1rem;
}


/* ------------Plans End------------ */









/* ------------Contact Start------------ */
#contact {
    background-image: url(https://us.images.westend61.de/0001308911pw/happy-business-people-having-a-meeting-in-office-UUF19917.jpg);
    background-position: top;
    background-size: cover;
}

#contact .overlay {
    width: 100%;
    min-height: 100vh;
    /* background-color: var(--main-color); */
    background-color: rgba(0, 56, 0, 0.758);
}

#contact .overlay h3 {
    font-size: 2.5rem;
    color: var(--white-color);
    margin-bottom: 2rem;
}

#contact .form-floating {
    margin-bottom: 1.5rem;
}

#contact .form-floating input {
    border-radius: .2rem;
}

#contact .form-floating label {
    left: 1rem;
    color: var(--text-color);
    font-weight: 700;
}

/* ------------Contact End------------ */











/* ------------Blog Start------------ */
#blog {
    background-color: var(--secondary-color);
}

#blog .card {
    padding: 0;
    border: 0;
    border-radius: .1rem;
    text-align: center;
}

#blog .card .card-body {
    position: relative;
}

#blog .card .box-container {
    width: 100%;
    position: absolute;
    top: -3rem;
    left: 0;
}

#blog .card .box {
    width: 6rem;
    height: 6rem;
    margin-left: 0;
    margin: auto;
    background-color: var(--white-color);
    color: var(--text-color);

    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transition: .5s;
}

#blog .card .box:hover {
    background-color: var(--main-color);
}

#blog .card .box:hover .date {
    color: white;
}

#blog .card .box .date {
    line-height: 2rem;
    font-size: 2rem;
    font-weight: 700;
    color: var(--main-color);
    transition: .5s;
}

#blog .card .box .month {
    font-weight: 600;
    color: var(--text-color);
    line-height: .9rem;
}

#blog .card .box .year {
    font-size: .8rem;
    color: var(--secondary-text-color);
}

/* ------------Blog End------------ */











/* ------------Footer Start------------ */
footer {
    background-color: var(--footer-color);
    color: var(--white-color);
}

footer .footer {
    padding: 2rem;
}

footer .footer h5 {
    font-weight: 700;
    margin: 3rem 0;
}

footer .footer li.list-group-item,
footer .footer a.list-group-item {
    background: transparent;
    border: 0;
    color: var(--white-colors);
}

footer .footer .list-group-item i {
    font-size: 1.3rem;
    margin-right: 1rem;
    color: var(--main-color);
}

footer .footer .social-icons {
    font-size: 2rem;
    color: var(--main-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

footer .footer .social-icons i {
    color: var(--white-color);
    padding: .5rem;
    border-radius: 50%;
    background-color: var(--main-color);
}

footer .bottom {
    background-color: black;
    padding: 1.3rem;
    color: var(--secondary-text-color);
}

/* ------------Footer End------------ */






@media only screen and (max-width: 600px) {
    .upper {
        width: 0;
        height: 0;

        visibility: hidden;

    }

    section {
        width: 100%;
        overflow-x: hidden;
    }

    #services .card-container,
    .testimonial .card-container,
    #plans .card-container,
    #blog .card-container {
        width: 100%;
        overflow-x: hidden;
        height: auto;
        grid-template-columns: auto;
    }

    .testimonial {
        /* position: static; */
        /* margin-top: 4rem;     */
        min-height: 100vh;
    }

    .testimonial .page-heading {
        padding: 0;
        line-height: 2rem;
    }

    .testimonial .page-heading h5 {
        font-size: 1.5rem;
        color: var(--white-color);
    }

    .testimonial .page-heading p {
        font-size: 3rem;
        font-weight: 800;
    }

    .testimonial .overlay {
        position: static;
        min-height: 100vh;
        padding-bottom: 2rem;
    }

    .testimonial .overlay .card-container {
        position: relative;
        top: 0;
        left: 0;
        gap: 1rem;
    }
}